<template> 
  <v-content id="background_alternative">
    <v-container fluid>
      <v-layout>
        <v-flex xs12 v-for="data_ in data"  v-bind:key="data_.Ticker">
          <v-card style="margin:2.5em;">
            <v-toolbar dark color="black">
              <v-toolbar-title>{{data_.Name}} ({{data_.Ticker}})</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>                
            <v-layout>
              <v-flex xs12 text-center>
                <v-table class="table table-hover" :data="data_.Predictions"
                  style="background:white;"
                >
                  <thead slot="head">
                    <th>Fecha</th>
                    <th>Precio Mximo Real</th>
                    <th>Precio Mximo Predicho</th>
                    <th>Precio Mnimo Real</th>
                    <th>Precio Mnimo Predicho</th>
                  </thead>
                  <tbody slot="body" slot-scope="{displayData}">
                    <tr v-for="row in displayData.slice(1,6)" :key="row.id">
                      <td>{{ row.Date }}</td>
                      <td>{{ row.Real_High_Value }}</td>
                      <td>{{ row.Predicted_High_Value }}</td>
                      <td>{{ row.Real_Low_Value }}</td>
                      <td>{{ row.Predicted_Low_Value }}</td>                    
                    </tr>
                  </tbody>
                </v-table>
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-content>
</template>

<script>

export default {
  name: 'SeePredictions',
  components: {
  },
  data () {
    return {
        data: null
    }
  },
  created(){
    this.predictions()
  },
  methods: {
      predictions: function() {
        this.axios
          .post('https://financialforecasting.herokuapp.com/get_predictions/',
          {username: 'admin', password: '123'})
          .then((response)=> {
            this.data = eval(response.data)
          })
      }
  }
}
</script>