<template> 
  <v-content id="background_alternative">
    <v-container >
      <v-layout>
        <v-flex>
          <v-toolbar style="padding:5px; box-sizing: border-box; width: 100vw; margin-left: calc(-50vw + 50%);" color="black" dark>
            <v-toolbar-title>
              {{symbol}}
              <v-spacer/>
              {{stock}}
            </v-toolbar-title>
          </v-toolbar>
        </v-flex>
      </v-layout>
    </v-container>
    <v-container fluid>
      <v-layout>
        <v-flex xs6>
          <h3>Precio Mximo: $1434</h3>
          <h3>Precio Mnimo: $1200</h3>
        </v-flex>
        <v-flex xs6 offset-xs4>
          <v-card class="stock_today" color="white">
            <v-flex xs12 >
              <v-flex class="last" xs12  style="padding:1em;">
                <h5>ltimo Precio de Cierre: {{last_close}}</h5>
              </v-flex>
              <v-flex class="last" xs12  style="padding:1em;">
                <h5>ltimo Precio de Apertura: {{last_open}}, Cambio del Precio de Apertura: {{change_open}} ({{porcentual_change_open}}%) </h5>
              </v-flex>
            </v-flex>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

    <div style="padding:2em;">
      <line-chart style="background:white;"
        :curve="false" 
        :data="stock_chart" 
        :min="stock_chart_min"
        :max="stock_chart_max"
        height="500px"
        />
    </div>
    <v-container>
      <v-flex xs10>
        <h3>Predicciones Histricas</h3>
        <v-table class="table table-hover" :data="historical_signal"
          style="background:white;"
        >
          <thead slot="head">
            <th>Fecha</th>
            <th>Precio Mximo Real</th>
            <th>Precio Mximo Predicho</th>
            <th>Precio Mnimo Real</th>
            <th>Precio Mnimo Predicho</th>
            <th>Error Porcential</th>
          </thead>
          <tbody slot="body" slot-scope="{displayData}">
            <tr v-for="row in displayData.slice(1,6)" :key="row.id">
              <td>{{ row.Date }}</td>
              <td>{{ row.Price_High }}</td>
              <td>{{ row.Predicted_Price_High }}</td>
              <td>{{ row.Price_Low }}</td>
              <td>{{ row.Predicted_Price_Low }}</td>
              <td>0%</td>
            </tr>
          </tbody>
        </v-table>
      </v-flex>
    </v-container>
  </v-content>
</template>

<script>
export default {
  name: 'StockAnalysis_',
  components: {
  },
  data () {
    return {
      stock_chart: [],
      stock_chart_min: 0,
      stock_chart_max: 0,
      stock: "",
      symbol: "",
      data: null,
      historical_signal: [],
      date: new Date().getTime(),
      /*CLOSE PRICE*/
      last_close: 0,
      /*OPEN PRICE*/
      last_open: 0,
      before_last_open: 0,
      change_open: 0,
      porcentual_change_open: 0
    }
  },
  created(){
    this.stock = this.$route.query.stock
    this.symbol = this.$route.query.symbol
  },
  mounted: function(){
    this.axios
          .get('https://financialforecasting.herokuapp.com/stock/'+this.symbol+'/')
          .then((response)=> {
            this.data = response.data;
            
            this.last_close = this.data[this.data.length-1].Open.toFixed(2)

            this.last_open = this.data[this.data.length-1].Open.toFixed(2);
            this.before_last_open = this.data[this.data.length-2].Open.toFixed(2);
            this.change_open = (this.last_open - this.before_last_open).toFixed(2);
            this.porcentual_change_open = (100*this.change_open/ this.before_last_open).toFixed(2);    


          /*FOR DISPLAY LINE CHART STOCK*/
          var data_close = {};           
          var data_high = {};
          var data_low = {};
          var data_open = {};
          this.data.forEach(element => {
            data_close[element.Date] = element.Close;
            data_high[element.Date] = element.High;
            data_low[element.Date] = element.Low;
            data_open[element.Date] = element.Open;
          });

          this.stock_chart_min = Math.floor(Math.min(
            Math.min(...Object.keys(data_close).map(key => data_close[key])), 
            Math.min(...Object.keys(data_high).map(key => data_high[key])), 
            Math.min(...Object.keys(data_low).map(key => data_low[key])), 
            Math.min(...Object.keys(data_open).map(key => data_open[key]))
          ));

          this.stock_chart_max = Math.ceil(Math.max(
            Math.max(...Object.keys(data_close).map(key => data_close[key])), 
            Math.max(...Object.keys(data_high).map(key => data_high[key])), 
            Math.max(...Object.keys(data_low).map(key => data_low[key])), 
            Math.max(...Object.keys(data_open).map(key => data_open[key]))
          ));

          this.stock_chart.push({
            name: 'Close', data: data_close
          });
          this.stock_chart.push({
            name: 'High', data: data_high,
          });
          this.stock_chart.push({
            name: 'Low', data: data_low,
          });  
          this.stock_chart.push({ 
            name: 'Open', data: data_open
          });

        
          /*FOR RANDOM DISPLAY*/    
          this.data.reverse().forEach(element => {
            this.historical_signal.push({
              'Date': element.Date.substring(0, 10), 
              'Price_High': element.High.toFixed(2), 'Predicted_Price_High': element.High.toFixed(2),
              'Price_Low': element.Low.toFixed(2), 'Predicted_Price_Low': element.Low.toFixed(2)
            })
          });
        });
  }
}
</script>