<template>
    <v-content id="background" :style="{backgroundImage: 'url(' + require('@/assets/Background.jpg') + ')' }">
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Ingreso al Sistema</v-toolbar-title>
                <v-spacer></v-spacer>                
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field 
                    prepend-icon="person" 
                    name="login" 
                    label="Usuario" 
                    type="text"
                    v-model="username" 
                    required
                    :rules="[username_valid || 'Usuario Incorrecto']">
                  </v-text-field>
                  <v-text-field 
                    prepend-icon="lock"
                    name="password" 
                    label="Contrasea" 
                    id="password" 
                    type="password" 
                    v-model="password"
                    required
                    :rules="[password_valid || 'Contrasea Incorrecta']">
                   </v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-flex xs6 text-center>
                  <router-link :to="{ name: 'CreateUser'}" >An no tienes una cuenta?</router-link>
                </v-flex>
                <v-flex xs6 text-center>
                  <v-btn color="primary" v-on:click="validate_users">Ingresar</v-btn>       
                </v-flex>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>

</template>

<script>
export default {
  name: 'Login_',
  components: {
  },
  data () {
    return {
      username_valid: true,
      username: '',
      password_valid: true,
      password: '',
      data: null
    }
  },
  methods: {
      validate_users: function() {
        this.axios.post('https://financialforecasting.herokuapp.com/validate_user/',
         {username: this.username, password: this.password}
        ).then((response)=> {
          this.data = response.data
          this.username_valid = true
          this.password_valid = true

          if(this.data.username != '' && this.data.role != ''){
            this.$user.set({'role': this.data.role})
            this.$router.push({name: 'Search'})
          }
          else{

            if(this.data.username != '' && this.data.username != undefined){
              this.password_valid = false        
            }
            else{
              this.password_valid = false
              this.username_valid = false
            }
          }
        })
      }
  }
}
</script>