import Login from './components/Login/Login.vue'
import Search from './components/Search/Search.vue'
import StockAnalysis from './components/StockAnalysis/StockAnalysis.vue'
import CreateUser from './components/CreateUser/CreateUser.vue'
import SeePredictions from './components/SeePredictions/SeePredictions.vue'
import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

var router = new VueRouter({
    routes: [
        {
            path: '/login', 
            name: 'Login',
            component: Login,
            meta: {
                permissions: [
                    {
                        role: "Admin",
                        access: false,
                        redirect: 'Search'
                    },
                    {
                        role: "User",
                        access: false,
                        redirect: 'Search'
                    }
                ]
            }
        },
        {
            path: '/search',
            name: 'Search',
            component: Search,
            meta: {
               permissions: [
                {
                    role: "Guest",
                    access: false,
                    redirect: 'Login'
                } 
               ]
            }
        },
        {
            path: '/stock_analysis',
            name: 'StockAnalysis',
            component: StockAnalysis,
            meta: {
                permissions: [
                    {
                        role: "Guest",
                        access: false,
                        redirect: 'Login'
                    }
                ]
            }
        },
        {
            path: '/create_user',
            name: 'CreateUser',
            component: CreateUser,
            meta: {
                permissions: [
                    {
                        role: "Admin",
                        access: false,
                        redirect: 'Search'
                    },
                    {
                        role: "User",
                        access: false,
                        redirect: 'Search'
                    }
                ]
            }
        },
        {
            path: '/see_predictions',
            name: 'SeePredictions',
            component: SeePredictions,
            meta: {
                permissions: [
                    {
                        role: "User",
                        access: false,
                        redirect: 'Search'
                    },
                    {
                        role: "Guest",
                        access: false,
                        redirect: 'Login'
                    }
                ]
            }
        },
        {
            path: '/',
            component: Login,
            meta: {
                permissions: [
                    {
                        role: "Admin",
                        access: false,
                        redirect: 'Search'
                    },
                    {
                        role: "User",
                        access: false,
                        redirect: 'Search'
                    }
                ]
            }
        }
    ]
})


export default router;