<template> 
  <v-content id="background_alternative">
    <v-container fluid>
      <v-layout>
        <v-flex xs12 offset-xs6>
          <v-card class="elevation-12">
            <v-toolbar dark color="orange">
              <v-toolbar-title>Buscar Acciones</v-toolbar-title>
              <v-spacer></v-spacer>                
            </v-toolbar>
            <v-layout>
              <v-spacer></v-spacer>
              <v-flex xs6 text-xs-left>
                <v-text-field v-model="stock" prepend-icon="stock" name="stock" label="Buscar" type="text"></v-text-field>
                
              </v-flex>
              <v-flex xs6 text-xs-right>
                <v-btn v-on:click="stock_search" dark color="black">Buscar</v-btn>
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <v-divider color="black"></v-divider>
    <v-container >
      <v-layout>
        <v-flex v-if="lookup == null || lookup[0] == undefined">
          <v-card style="margin:1em;">
            <v-toolbar dark color="red">
              <v-toolbar-title>Nombre de accin invalido</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>
            <v-layout style="margin:1em;">
              <p>Buscar un nombre de accin correcta.</p>
            </v-layout>
             
          </v-card>
        </v-flex>
        <v-flex xs12 v-else>
          <v-flex xs12 v-for=" lookup_ in lookup"  v-bind:key="lookup_.symbol">
          <v-card style="margin:2.5em;">
            <v-toolbar dark color="black">
              <v-toolbar-title>{{lookup_.symbol}}</v-toolbar-title>
              <v-spacer></v-spacer>
            </v-toolbar>                
            <v-layout>
              <v-flex xs6 text-center>
                <h1>{{lookup_.name}}</h1>      
              </v-flex>
              <v-flex xs6 text-center>
                <v-btn :to="{ name: 'StockAnalysis'
                            , query: { symbol: lookup_.symbol, stock: lookup_.name } }" 
                            dark color="red">
                  Ir
                </v-btn>
              </v-flex>
            </v-layout>
          </v-card>
          </v-flex>
        </v-flex>
      </v-layout> 
    </v-container>
  </v-content>
</template>

<script>

export default {
  name: 'Search_',
  components: {
  },
  data () {
    return {
        lookup: null,
        stock: ''
    }
  },
  created(){
    this.stock = this.$route.query.stock
    this.stock_search()
  },
  methods: {
      stock_search: function() {
        this.axios
          .get('https://financialforecasting.herokuapp.com/lookup/'+this.stock+'/')
          .then((response)=> (this.lookup = response.data))
      }
  }
}
</script>